import React, { Component } from "react";

import UserDataService from "../services/user.service";
import LocationDataService from "../services/location.service";

import mapboxgl from 'mapbox-gl/dist/mapbox-gl-csp';
// eslint-disable-next-line import/no-webpack-loader-syntax
import MapboxWorker from 'worker-loader!mapbox-gl/dist/mapbox-gl-csp-worker';
 

mapboxgl.workerClass = MapboxWorker;
mapboxgl.accessToken = process.env.REACT_APP_MAPBOX_ACCESS_TOKEN;

export default class Map extends Component {
  constructor(props) {
    super(props);
    this.retrievePublishedLocations = this.retrievePublishedLocations.bind(this);
    this.formatGeoJSON = this.formatGeoJSON.bind(this);
    this.initMap = this.initMap.bind(this);

    this.state = {
      locations: [],
      currentLocation: null,
      lat: -37.81333017785542,
      lng: 144.9614772172758,
      zoom: 9,
      map:null
    };
    this.mapContainer = React.createRef();
  }
  

  componentDidMount() {
    this.retrievePublishedLocations();
    
  }

  componentDidUpdate() {

  }

  retrievePublishedLocations() {
    LocationDataService.getAll()
      .then(response => {
        console.log(response.data);
        this.setState({
          locations: this.formatGeoJSON(response.data)
        });
        this.initMap();
        console.log(this.state.locations);
      })
      .catch(e => {
        console.log(e);
      });
  }

  initMap() {
    const { lng, lat, zoom } = this.state;
    const map = new mapboxgl.Map({
      container: this.mapContainer.current,
      style: 'mapbox://styles/mapbox/light-v9',
      center: [lng, lat],
      zoom: zoom
    });

    this.setState({
      map : map
    });

    map.on('move', () => {
      this.setState({
        lng: map.getCenter().lng.toFixed(4),
        lat: map.getCenter().lat.toFixed(4),
        zoom: map.getZoom().toFixed(2)
      });
    });
    
    map.on('load', () => {
      map.addSource('locations', {
        type: 'geojson',
        data: this.state.locations
      });

      // Add a layer showing the places.
      map.addLayer({
        'id': 'locations',
        'type': 'symbol',
        'source': 'locations',
        'layout': {
          'icon-image': 'marker-15',
          'icon-allow-overlap': true,
          'icon-size':1
        }
      });
    });

    // When a click event occurs on a feature in the places layer, open a popup at the
// location of the feature, with description HTML from its properties.
    map.on('click', 'locations', function (e) {
      var coordinates = e.features[0].geometry.coordinates.slice();
      var description = e.features[0].properties.description;
       
      // Ensure that if the map is zoomed out such that multiple
      // copies of the feature are visible, the popup appears
      // over the copy being pointed to.
      while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
      coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
    }
     
    new mapboxgl.Popup()
      .setLngLat(coordinates)
      .setHTML(description)
      .addTo(map);
    });
     
    // Change the cursor to a pointer when the mouse is over the places layer.
    
    map.on('mouseenter', 'locations', function () {
      map.getCanvas().style.cursor = 'pointer';
    });
     
    // Change it back to a pointer when it leaves.
    map.on('mouseleave', 'locations', function () {
      map.getCanvas().style.cursor = '';
    });

    
 

  }

  formatGeoJSON(locations) {
    
    var obj = locations.map(function(location) {
      return {
        'type': 'Feature',
        'properties': {
          'description': '<h4>' + location.title + '</h4><div>' + location.description + '</div>'   
        },
        'geometry': {
          'type': 'Point',
          'coordinates': [location.longitude,location.latitude]
        }
      }
    });

    var geoLocations = {
      "type": "FeatureCollection",
      "features": obj
    }
    console.log(obj);
    return geoLocations;

  }


  render() {
    return (
      <div className="container">
      <div className="map-sidebar">
        Longitude: {this.state.lng} | Latitude: {this.state.lat} | Zoom: {this.state.zoom}
      </div>
        <div id="map" ref={this.mapContainer} className="map-container"></div>
      </div>
    );
  }
}